* {
  font-family: Arial, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
a:visited,
a {
  color: inherit;
  text-decoration: none;
}

.Home {
  height: calc(100vh - 60px);
  display: flex;
  flex-direction: column;
  .home-preview {
    width: clamp(320px, 100vw, 600px);
    min-height: 400px;

    background-image: url("../assets/supercars.jpeg");
    background-size: cover;
    background-position: center;
    color: white;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    h1 {
      font-family: Georgia, "Times New Roman", Times, serif;
      background-color: rgb(0, 0, 0, 0.5);
      padding: 2px;
      border-radius: 2.5px;
      font-size: 25px;
      margin-top: 10px;
      font-weight: 500;
    }

    .preview-buttonContainer {
      margin-bottom: 20px;

      display: flex;
      gap: 25px;
      .preview-button {
        background-color: rgba(255, 255, 255, 0.8);
        color: black;
        font-weight: bold;
        padding: 15px;
        border-radius: 2.5px;
        cursor: pointer;
      }
    }
  }

  .home-options {
    width: 100%;

    display: flex;
    justify-content: center;
    div {
      flex: 1;
      text-align: center;
      padding: 20px 0 20px 0;

      display: flex;
      flex-direction: column;
      .icon {
        color: black;
        margin-bottom: 10px;
      }
    }
    @media (hover: hover) {
      div:hover {
        cursor: pointer;
        background-color: darkgray;
      }
    }
  }
}

@media (min-width: 600px) {
  .Home {
    .home-preview {
      width: 100%;
      height: clamp(250px, 100vh, 500px);
      background-image: url("../assets/supercars_large.jpeg");

      h1 {
        border-radius: 2.5px;
        font-size: 35px;
      }
      .preview-button {
        padding: 20px;
      }
    }
  }
}
